import React from 'react';
import { Navbar, Container } from 'react-bootstrap';

export default function NavBar() {
  return (
    <Navbar bg="dark" variant="dark">
      <Container fluid>
        <Navbar.Brand href="#edvora">Edvora</Navbar.Brand>
        <Navbar.Toggle />
        <Navbar.Collapse className="justify-content-end">
          <Navbar.Text>
            <div className="d-flex justify-content-center align-items-center pe-3">
              <h6 className="mb-0 pe-2">Nikhil Sai</h6>
              <img className="mb-0" style={{borderRadius: "50%", height: "30px", width: "30px"}} src='data:image/jpeg;base64,' alt='user'/>
            </div>
          </Navbar.Text>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}
